/* main.css */
@import url('../styles/fonts.css');
@import url('../styles/header.module.css');
@import url('../styles/footer.module.css');

:root {
  /* Базовые настройки */
  --font-family-main: 'Open Sans', -apple-system, BlinkMacSystemFont, sans-serif;
  
  /* Цвета */
  --color-primary: #3980ff;
  --color-secondary: #eb5757;
  --color-light: #fef4ff;
  --color-dark: #0c4ab3;
  --color-text: #333;
  --color-white: #fff;

  /* Размеры шрифтов */
  --font-size-base: 1rem; /* 16px */
  --font-size-h1: 3.4375rem; /* 55px */
  --font-size-h2: 2.1875rem; /* 35px */
  --font-size-h3: 1.5rem;    /* 24px */
  --font-size-text: 1.25rem; /* 20px */
  --font-size-small: 1rem;   /* 16px */

  /* Отступы */
  --spacing-xs: 0.5rem;
  --spacing-sm: 1rem;
  --spacing-md: 1.5rem;
  --spacing-lg: 2rem;
  --spacing-xl: 3rem;

  /* Брейкпоинты */
  --breakpoint-mobile: 480px;
  --breakpoint-tablet: 768px;
  --breakpoint-laptop: 1024px;
  --breakpoint-desktop: 1280px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-family-main);
  font-size: var(--font-size-base);
  line-height: 1.5;
  color: var(--color-text);
  background-color: var(--color-white);
  overflow-x: hidden;
}

.container {
  background-color: var(--color-light);
  border-radius: 27px;
  padding: 0;
  max-width: 1793px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}

/* Main content styles */
.main {
  width: 100%;
  max-width: 1733px;
  padding: 0 var(--spacing-md);
  margin: 0 auto;
}

.mainTitle {
  text-align: left;
  margin: 0;
}

/* Introduction section */
.introduction {
  background-color: var(--color-primary);
  border-radius: 20px;
  color: var(--color-white);
  margin: 0 auto;
  padding: var(--spacing-xl) 0;
  width: 100%;
}

.introduction .content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: var(--spacing-xl);
  max-width: 1408px;
  width: 100%;
  padding: 0 var(--spacing-md);
  margin: 0 auto;
}

.introduction .textBox {
  margin-top: var(--spacing-md);
  max-width: 585px;
  text-align: left;
}

.introduction .textBox h1 {
  font-size: var(--font-size-h1);
  font-weight: 300;
  margin-bottom: var(--spacing-xl);
  line-height: 1.2;
}

.introduction .textBox p {
  font-size: 1.125rem;
  margin-bottom: var(--spacing-md);
}

.introduction .iconsText {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-lg);
  max-width: 493px;
  margin-bottom: var(--spacing-lg);
}

.introduction .iconDescription {
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
}

.introduction .icons img {
  width: 40px;
  height: 40px;
  flex-shrink: 0;
}

.introduction .button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 73px;
  background-color: var(--color-secondary);
  color: var(--color-white);
  border: none;
  border-radius: 10px;
  font-size: var(--font-size-text);
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
}

.introduction .button:hover {
  background-color: #f34141;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(235, 87, 87, 0.3);
}

.introduction .picture {
  max-width: 686px;
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.introduction .picture img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

/* Services section */
.services {
  margin: 0 auto;
  padding: var(--spacing-xl) 0;
  width: 100%;
}

.services .content {
  max-width: 1500px;
  width: 100%;
  padding: 0 var(--spacing-md);
  margin: 0 auto;
}

.services .content .title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: var(--spacing-md);
  max-width: 773px;
  margin: 0 auto var(--spacing-xl) 0;
}

.services .content .title h1 {
  font-size: var(--font-size-h1);
  font-weight: 400;
  max-width: 330px;
  width: 100%;
  line-height: 1.2;
}

.services .content .title p {
  font-size: var(--font-size-small);
  max-width: 400px;
  width: 100%;
  color: var(--color-text);
}

.serviceGrid {
  max-width: 1290px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--spacing-md);
  margin-top: var(--spacing-xl);
}

.serviceCard {
  background-color: var(--color-white);
  border-radius: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  padding: var(--spacing-lg);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.serviceCard:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.cardContent {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.heading {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
  align-items: center;
  margin-bottom: var(--spacing-md);
}

.heading img {
  max-width: 100%;
  height: auto;
}

.ovalContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: var(--spacing-xs);
  margin-top: var(--spacing-md);
}

.oval {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.25);
  border-radius: 54px;
  padding: var(--spacing-xs) var(--spacing-sm);
  font-size: var(--font-size-small);
  color: var(--color-text);
  white-space: nowrap;
}

/* Problem Solving section */
.problemSolving {
  background-color: var(--color-primary);
  color: var(--color-white);
  border-radius: 20px;
  margin: 0 auto;
  width: 100%;
}

.problemSolving .content {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
  max-width: 1408px;
  width: 100%;
  padding: var(--spacing-md) var(--spacing-md);
  margin: 0 auto;
}

.problemSolving .content .title {
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
}

.problemSolving .content .title h1 {
  font-size: var(--font-size-h1);
  font-weight: 300;
  line-height: 1.2;
  margin-bottom: var(--spacing-md);
}

.problemSolving .grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  gap: var(--spacing-md);
}

.problemSolving .mobilePhone {
  grid-column: 2;
  display: flex;
  justify-content: center;
  align-items: center;
}

.problemSolving .mobilePhone img {
  max-width: 100%;
  height: auto;
}

.textBox {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin-bottom: var(--spacing-xl);
}

.pointTitle h3 {
  font-size: var(--font-size-h3);
  font-weight: 300;
  line-height: 1.3;
  margin-bottom: var(--spacing-xs);
}

.pointDesc p {
  font-size: 0.9375rem;
  line-height: 1.5;
}

.pointTitle {
  display: flex;
  align-items: center;
  margin-bottom: var(--spacing-sm);
}

.point {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  background-color: var(--color-dark);
  color: var(--color-white);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: var(--spacing-sm);
  font-size: 1.25rem;
  flex-shrink: 0;
}

/* Responsive adjustments */
@media (max-width: 1280px) {
  .introduction .content {
    gap: var(--spacing-lg);
    padding: 0 var(--spacing-sm);
  }

  .problemSolving .grid {
    gap: var(--spacing-sm);
  }

  .text:first-child {
    margin-left: 0;
  }
}

@media (max-width: 1024px) {
  :root {
    --font-size-h1: 2.5rem;
    --font-size-h2: 1.75rem;
    --font-size-h3: 1.25rem;
  }

  .introduction .content {
    grid-template-columns: 1fr;
    gap: var(--spacing-lg);
  }

  .introduction .textBox {
    margin-top: 0;
    max-width: 100%;
    text-align: center;
  }

  .introduction .iconsText {
    margin: var(--spacing-lg) auto;
  }

  .services .content .title {
    flex-direction: column;
    text-align: center;
    margin: 0 auto var(--spacing-lg);
  }

  .services .content .title h1,
  .services .content .title p {
    max-width: 100%;
    text-align: center;
  }

  .problemSolving .grid {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .problemSolving .mobilePhone {
    grid-column: auto;
    order: -1;
    margin-bottom: var(--spacing-lg);
  }

  .textBox {
    max-width: 100%;
    align-items: center;
    text-align: center;
    margin-bottom: var(--spacing-lg);
  }

  .pointTitle {
    justify-content: center;
  }

  .pointDesc p {
    text-align: center;
  }
}

@media (max-width: 768px) {
  :root {
    --font-size-h1: 2rem;
    --font-size-text: 1rem;
  }

  .container {
    border-radius: 0;
  }

  .introduction,
  .services,
  .problemSolving {
    padding: var(--spacing-lg) 0;
    border-radius: 0;
  }

  .introduction .button {
    height: 60px;
  }

  .serviceGrid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  :root {
    --font-size-h1: 1.75rem;
    --font-size-text: 0.875rem;
  }

  .main {
    padding: 0 var(--spacing-xs);
  }

  .introduction .textBox h1,
  .services .content .title h1,
  .problemSolving .content .title h1 {
    margin-bottom: var(--spacing-md);
  }

  .oval {
    padding: 0.25rem 0.75rem;
    font-size: 0.875rem;
  }
}

/* Utility classes */
.text-center {
  text-align: center;
}

.mb-sm {
  margin-bottom: var(--spacing-sm);
}

.mb-md {
  margin-bottom: var(--spacing-md);
}

.mb-lg {
  margin-bottom: var(--spacing-lg);
}

.mb-xl {
  margin-bottom: var(--spacing-xl);
}